	<v-container fluid>
		<v-item-group v-model="selFSA">
			<v-row>
				<template v-if="currentFSA == 0">
					<v-col cols="12" md="4">
						<v-item v-slot:default="{ active, toggle }">
							<v-card
								:color="active ? 'green accent-4' : ''"
								:dark="active"
								height="200"
								@click="toggle"
							>
								<div
									class="pa-1 headline font-weight-light text-center"
									style="position:absolute;top:50%;left:8px;right:8px;transform:translateY(-50%)"
								>
