									</div>
								</v-scroll-y-transition>
							</v-card>
						</v-item>
					</v-col>
				</template>
				<template v-else>
					<v-col cols="12" md="4">
						<v-item v-slot:default="{ active, toggle }">
							<v-card
								:color="active ? 'red accent-4' : ''"
								:dark="active"
								height="200"
								@click="toggle"
							>
								<div
									class="pa-1 headline font-weight-light text-center"
									style="position:absolute;top:50%;left:8px;right:8px;transform:translateY(-50%)"
								>
									Stop FSA Participation
								</div>
								<v-scroll-y-transition>
									<div v-if="active" class="display-3 text-right px-3">
										<v-icon x-large>mdi-check</v-icon>
									</div>
								</v-scroll-y-transition>
							</v-card>
						</v-item>
					</v-col>
				</template>
				<v-col cols="12" md="4">
					<v-item v-slot:default="{ active, toggle }">
						<v-card
							:color="active ? 'primary' : ''"
							:dark="active"
							height="200"
							@click="toggle"
						>
							<div
								class="pa-1 headline font-weight-light text-center"
								style="position:absolute;top:50%;left:8px;right:8px;transform:translateY(-50%)"
							>
								REDUCE Annual Election
							</div>
							<v-scroll-y-transition>
								<div v-if="active" class="display-3 text-right px-3">
									<v-icon x-large>mdi-check</v-icon>
								</div>
							</v-scroll-y-transition>
						</v-card>
					</v-item>
				</v-col>
				<v-col cols="12" md="4">
					<v-item v-slot:default="{ active, toggle }">
						<v-card
							:color="active ? 'primary' : ''"
							:dark="active"
							height="200"
							@click="toggle"
						>
							<div
								class="pa-3 headline font-weight-light text-center"
								style="position:absolute;top:50%;left:8px;right:8px;transform:translateY(-50%)"
							>
