<script>
	export default {
		name: 'theselection',
		data() {
			return {
				e1: 1,
				selFSA: null,
			};
		},
		props: ['currentFSA'],
		created: function() {
			this.$emit('selected', this.selectedMod);
			this.$emit('selectedfsa', this.selFSA);
		},
		watch: {
			selFSA: function() {
				this.$emit('selected', this.selectedMod);
				this.$emit('selectedfsa', this.selFSA);
			},
		},
		computed: {
			selectedMod() {
				switch (this.selFSA) {
					case 0:
						return 'Start New FSA';

					case 1:
						return 'Stop FSA Participation';

					case 2:
						return 'REDUCE Annual Election';

					case 3:
						return 'Increase Annual Election';

					default:
						return 'Make a selection...';
				}
			},
		},
	};
</script>
