<template>
	<v-app>
		<div>
			<v-app-bar color="transparent" flat dense :dark="goDark">
				<v-spacer></v-spacer>

				<v-spacer></v-spacer>

				<v-menu left bottom>
					<template v-slot:activator="{ on }">
						<v-btn icon v-on="on">
							<v-icon>mdi-dots-vertical</v-icon>
						</v-btn>
					</template>

					<v-list>
						<v-list-item @click="goDark = false">
							<v-list-item-title>Light</v-list-item-title>
						</v-list-item>
						<v-list-item @click="goDark = true">
							<v-list-item-title>Dark</v-list-item-title>
						</v-list-item>
						<v-divider></v-divider>
						<v-list-item @click="currentFSASel = 0">
							<v-list-item-title>Not Participating in FSA</v-list-item-title>
						</v-list-item>
						<v-list-item @click="currentFSASel = 1">
							<v-list-item-title>Participating in FSA</v-list-item-title>
						</v-list-item>
					</v-list>
				</v-menu>
			</v-app-bar>
		</div>
		<v-content>
			<v-container fluid>
				<v-row align="center" justify="center">
					<v-col cols="12" sm="8">
						<div class="display-1 font-weight-light mb-3">FSA Modification</div>
					</v-col>
				</v-row>
				<v-row align="center" justify="center">
					<v-col cols="12" sm="8">
						<v-card :loading="submitting">
							<v-card-title
								class="title font-weight-regular justify-space-between"
							>
								<div>
									<div>{{ currentTitle }}</div>
									<div v-if="step < 4" class="subtitle-1">{{ currentMod }}</div>
								</div>
								<v-avatar
									color="primary lighten-2"
									class="subheading white--text"
									size="24"
									v-text="step"
								></v-avatar>
							</v-card-title>
							<v-window v-model="step">
								<v-window-item :value="1">
									<v-card-text>
										<theSelection
											:currentFSA="currentFSASel"
											@selected="selectModMethod"
											@selectedfsa="selectMod"
											:currentHSA="true"
										></theSelection>
									</v-card-text>
								</v-window-item>
								<v-window-item :value="2">
									<v-card-text
										>Lorem ipsum dolor sit amet consectetur adipisicing elit.
										Impedit placeat aut repudiandae aperiam quo ad, non nesciunt
										nostrum quisquam eaque, minus ratione quis voluptates fugit
										dolores iusto odio nisi possimus.</v-card-text
									>
								</v-window-item>

								<v-window-item :value="3">
									<div class="pa-4 text-center">
										Lorem ipsum dolor sit amet consectetur adipisicing elit.
										Deserunt, quaerat quo explicabo eum fugit ducimus
										doloremque. Omnis reiciendis repellat voluptate.
									</div>
								</v-window-item>
								<v-window-item :value="4">
									<div class="pa-4 text-center">
										<v-progress-circular
											size="70"
											color="primary"
											indeterminate
										></v-progress-circular>
									</div>
								</v-window-item>
								<v-window-item :value="5">
									<div class="pa-4 text-center">
										<div>
											<i class="fad fa-check fa-5x success--text"></i>
										</div>
										<h3 class="title font-weight-light mb-2">
											Changes succesfully submitted!
										</h3>
									</div>
								</v-window-item>
							</v-window>
							<v-divider></v-divider>
							<template v-if="step < 4">
								<v-card-actions>
									<v-btn v-if="step > 1 && step < 4" text @click="step--"
										>Back</v-btn
									>
									<v-spacer></v-spacer>
									<v-btn
										:disabled="step > 3 || selectedFsa == null"
										v-if="step < 4"
										color="primary"
										depressed
										@click="step++"
										>Next</v-btn
									>
								</v-card-actions>
							</template>
						</v-card>
					</v-col>
				</v-row>
			</v-container>
		</v-content>
	</v-app>
</template>

<script>
	import theSelection from '@/components/theSelection.vue';
	export default {
		name: 'fsa',
		components: {
			theSelection,
		},
		data: () => ({
			step: 1,
			currentMod: '',
			selectedFsa: '',
			goDark: false,
			submitting: false,
			currentFSASel: 1,
		}),
		methods: {
			changeHandler(value) {
				console.log('Value changed to: ', value);
			},
			selectModMethod(value) {
				this.currentMod = value;
			},
			selectMod(value) {
				this.selectedFsa = value;
			},
		},
		watch: {
			step: function() {
				var self = this;
				if (self.step == 4) {
					self.submitting = true;
					setTimeout(function() {
						self.step = 5;
					}, 3000);
				} else {
					this.submitting = false;
				}
			},
			goDark: function() {
				if (this.goDark) {
					this.$vuetify.theme.dark = true;
				} else {
					this.$vuetify.theme.dark = false;
				}
			},
		},
		computed: {
			currentTitle() {
				switch (this.step) {
					case 1:
						return 'Type of Modification';
					case 2:
						return 'Make your modification';
					case 3:
						return 'Review';
					case 4:
						return 'Submitting';
					default:
						return 'Confirmation';
				}
			},
		},
	};
</script>
